<template>
	<el-select v-model="selectLabel" 
		@remove-tag="removeTag" 
		:multiple="multipleChoice" 
		:clearable="clearable" 
		@clear="clear"
	>
		<el-input
		v-show="filterable"
		class="search-input"
		:placeholder="placeholder"
		v-model="filterText">
		</el-input>
		<el-option value="" label="" class="treeData">
			<el-tree
				id="tree-option"
				ref="selectTree"
				:accordion="accordion"
				:data="treeData"
				:show-checkbox="multipleChoice"
				:props="props"
				:check-on-click-node="!multipleChoice"
				:check-strictly="checkStrictly"
				:node-key="props.value"    
				:highlight-current="false"
				:filter-node-method="filterNode"
				:lazy="lazy"
				:load="lazyLoad"
				@check="handleNodeClick">
			</el-tree>
		</el-option>
		<!-- 多选时通过将选中项设置成下拉选项保证selectLabel的唯一性 -->
		<el-option v-show="false" :value="item[props.value]" :label="item[props.label]" v-for="item in selectData" :key="item[props.value]"></el-option>
	</el-select>
</template>

<script>
export default {
	name: "EleTreeSelect",
	props:{
		// 配置项(字段映射)
		props:{
		type: Object,
		default:()=>{
			return {
				value:'id',
				label: 'title',
				children: 'children',
				isLeaf: 'leaf'
			}
		}
		},
		// 作为一棵树的唯一标志返回父级
		code: {
			type: String,
			default: () => ''
		},
		// 数据
		treeData:{
			type: Array,       
			default: () => []
		},
		// 是否可清空选项
		clearable:{
			type: Boolean,
			default: () => true
		},
		// 是否每次只打开一个同级树节点展开
		accordion:{
			type: Boolean,
			default: () => false
		},
		// 是否多选
		multipleChoice: {
			type: Boolean,
			default: () => true
		},
		// 是否懒加载
		lazy: {
			type: Boolean,
			default: () => false
		},
		// 多选时是否父子节点不互相关联
		checkStrictly: {
			type: Boolean,
			default:() => true
		},
		// 每一项的label是否可能重复
		labelRepeat: {
			type: Boolean,
			default: () => false
		},
		// 是否可搜索
		filterable: {
			type: Boolean,
			default: () => false
		},
		// 搜索框placeholder
		placeholder: {
			type: String,
			default: () => '输入关键字'
		}
	},
	data() {
		return {
			filterText: '', // 进行筛选的内容
			selectLabel: null, // 选中项对应的label，用于选择框展示
			selectData: [], // 存储选中的item，用于扩展
		}
	},
	mounted() {
		this.initScrollBar();
	},
	watch: {
		filterText(val) {
			this.$refs.selectTree.filter(val);
		}
	},
	methods: {
		// 初始化滚动条样式
		initScrollBar(){
			this.$nextTick(()=>{
				let dropdownWrap = document.querySelectorAll('.el-select-dropdown__wrap');
				let scrollBar = document.querySelectorAll('.el-scrollbar__bar');
				dropdownWrap.forEach(ele => ele.style.cssText = 'max-height: none; overflow: hidden;');
				scrollBar.forEach(ele => ele.style.width = 0);
			})
		},
		// 从整棵树data中查找返回选中项
		findCheckItem(treeData, checkedVal) {
			for (let item of treeData) {
				if (item[this.props.value] === checkedVal) {
					return item;
				} else {
					if (item[this.props.children]) {
						let checkedItem = this.findCheckItem(item[this.props.children], checkedVal)
						if (checkedItem) return checkedItem;
					}
				}
			}
		},
		// 多选时设置选择框的展示数据
		setCheckedLabel(checkedData) {
			// this.selectLabel = []; // 用于select框展示的数据
			this.selectLabel = checkedData; // 用于select框展示的数据
			this.selectData = []; // 保存选中项
			for (let item of checkedData) {
				const checkedItem = this.findCheckItem(this.treeData, item);
				// this.selectLabel.push(checkedItem.name);
				this.selectData.push(checkedItem);
			}
			this.$emit('getCheckedItem', this.selectData,  this.code) // 返回选中项的所有数据
		},
		// 多选
		handleNodeClick(){
			const checkedData = this.$refs.selectTree.getCheckedKeys();
			this.setCheckedLabel(checkedData);
			this.$emit('getCheckedData',checkedData, this.code) // 返回选中项的value
		},
		// 懒加载
		lazyLoad(node, resolve) {
			this.$emit('lazyLoad', node, resolve, this.code)
		},
		// select框移除单个标签时触发
		removeTag(val) {
			this.$refs.selectTree.setChecked(val, false);
			this.selectData = this.selectData.filter(item => {
				return item[this.props.value] !== val;
			})
			this.$emit('getCheckedData', this.selectLabel, this.code) // 返回选中项的value
			this.$emit('getCheckedItem', this.selectData, this.code) // 返回选中项的所有数据
		},
		// 清除选中
		clear(){
			this.selectLabel = null;
			this.selectData = [];
			this.$refs.selectTree.setCheckedKeys([]);
			this.$emit('getCheckedData', this.selectLabel, this.code); // 返回选中项的value
			this.$emit('getCheckedItem', this.selectData, this.code); // 返回选中项的所有数据
		},
		// 树节点筛选
		filterNode(value, data) {
			if (!value) return true;
			return data[this.props.label].indexOf(value) !== -1;
		}
	},
};
</script>

<style scoped>
	.el-scrollbar .el-scrollbar__view .el-select-dropdown__item{
		height: auto;
		max-height: 274px;
		padding: 0;
		overflow: hidden;
		overflow-y: auto;
	}
	.el-select-dropdown__item.selected{
		font-weight: normal;
	}
	ul li >>>.el-tree .el-tree-node__content{
		height:auto;
	}
	.el-tree-node__label{
		font-weight: normal;
	}
	.search-input{
		height: 30px;
		padding: 0 10px;
		box-sizing: border-box;
	}
	.search-input >>> .el-input__inner {
		height: 30px;
		line-height: 30px;
	}
</style>

