// 插件是一个函数时会被当做install方法
/** require.context()
 * @description: require.context是webpack的一个API
 * @param {第一个参数} 目标文件夹
 * @param {第二个参数} Boolean 是否匹配子文件夹
 * @param {第三个参数} 正则表达式规定要匹配的文件类型
 * @return {*}
 */
const requireComponent = require.context('./', true, /\.vue$/)
const install = function (Vue) {
  requireComponent.keys().forEach(fileName => {
    // 当前组件
    const file = requireComponent(fileName)
    // 当前组件名称
    const componentName = file.default.name
    Vue.component(componentName, file.default || file)
  })
}
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}
export default {
  install
}